.user-container {
  position: relative;
}
.user-container button[mat-icon-button] {
  padding: 0;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--mat-sys-on-primary);
}
.user-container button[mat-icon-button] img {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  object-fit: cover;
}
.user-container button[mat-icon-button] .mat-icon {
  font-size: 28px;
  width: 28px;
  height: 28px;
}
.user-container .status-dot {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-right: 12px;
}
.user-container .status-dot.online {
  background-color: #22c55e;
}
.user-container .status-dot.away {
  background-color: #f59e0b;
}
.user-container .status-dot.busy {
  background-color: #ef4444;
}
.user-container .status-dot.not-visible {
  background-color: #9ca3af;
}

.user-menu button[mat-menu-item], .status-menu button[mat-menu-item] {
  display: flex;
  align-items: center;
}
.user-menu button[mat-menu-item] .mat-icon, .status-menu button[mat-menu-item] .mat-icon {
  margin-right: 12px;
  color: var(--mat-sys-on-surface);
}
.user-menu .mat-divider, .status-menu .mat-divider {
  border-top-color: rgba(var(--mat-sys-on-surface), 0.12);
}

.status-dot {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-right: 12px;
}
.status-dot.online {
  background-color: #22c55e;
}
.status-dot.away {
  background-color: #f59e0b;
}
.status-dot.busy {
  background-color: #ef4444;
}
.status-dot.not-visible {
  background-color: #9ca3af;
}

.user-menu, .status-menu {
  min-width: 200px;
  padding: 8px 0;
}
.user-menu button[mat-menu-item], .status-menu button[mat-menu-item] {
  display: flex;
  align-items: center;
}
.user-menu button[mat-menu-item] .mat-icon, .status-menu button[mat-menu-item] .mat-icon {
  margin-right: 12px;
}
